import uniSection from '../../components/uni-section/uni-section.vue';
import { request } from '../../utils/request.js';
export default {
	components: { uniSection },
	data() {
		return {
			// 微信昵称
			nickname: '',
			center: {},
			// 主播数
			num: '',
			// 总收益
			all: '',
			// 可提现金额
			ablemoney: '',
			// 提现模态框的显示隐藏
			showMonmodal: false,
			// 合同签署框的显示隐藏
			contractShow: false,
			// 用户信息
			userinfo: {},
			token: '',
			form: {
				cashMoney: ''
			}
		};
	},
	mounted() {
		this.getToken();
		this.getuserInfo();
		this.getMineinfo();
	},
	onShareAppMessage(res) {
		let pid = JSON.parse(uni.getStorageSync('login')).user_id;
		if (res.from === 'button') {
			// 来自页面内分享按钮
		}
		return {
			title: '天天提现',
			path: `/pages/anchor/anchor?pid=${pid}`,
			imgUrl: 'https://ww1.yunjiexi.club/2020/03/18/GwFBk.png'
		};
	},
	methods: {
		// 展示合同签署框
		showContract() {
			this.contractShow = true;
		},
		// 关闭
		closecontract() {
			this.contractShow = false;
		},
		// 获取token
		getToken() {
			this.token = uni.getStorageSync('token');
		},
		// 获取用户相关信息
		getMineinfo() {
			const that = this;
			request({
				url: '/api/user/userCenter'
			}).then(res => {
				if (res.code == 401) {
					uni.reLaunch({
						url: '/pages/authorise/authorise'
					});
				}
				that.ablemoney = res.data.data.able_money.toFixed(2);
				that.num = res.data.data.anchor_num.toString();
				that.all = Number(res.data.data.money).toFixed(2);
			});
		},
		getuserInfo() {
			uni.getUserInfo({
				provider: 'weixin',
				success: result => {
					this.userinfo = result.userInfo;
				},
				fail: error => {
					uni.reLaunch({
						url: '/pages/authorise/authorise'
					});
				}
			});
		},
		getMoney() {
			this.showMonmodal = true;
		},
		handleContact(e) {
			console.log(e.detail.path);
			console.log(e.detail.query);
		},
		closeModal() {
			this.showMonmodal = false;
		},
		// 关于模态框的函数
		withdraw() {
			const that = this;
			if (that.form.cashMoney <= that.ablemoney) {
				request({
					url: '/api/user/applyWithdraw',
					method: 'POST',
					data: {
						money: that.form.cashMoney
					}
				}).then(res => {
					if (res.code == 1) {
						uni.showToast({
							title: res.data.msg,
							icon: 'success'
						});
						// 如果提示请登录后操作，跳转到授权页
					} else if (res.code == 401) {
						uni.reLaunch({
							url: '/pages/authorise/authorise'
						});
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						});
					}
					that.getMineinfo();
					that.form.cashMoney = '';
				});
			} else {
				uni.showToast({
					title: '超出可提现金额，请重新填写',
					icon: 'none'
				});
				setTimeout(() => {
					this.showMonmodal = true;
				}, 2000);
			}
		}
	}
};
